<template>
    <div>
        <el-upload
            :action="url"
            :headers="config.IMG_UPLOUD_REQUEST_HEADERS.headers(true)"
            list-type="picture-card"
            :index="index"
            :name="name"
            :data="uploadObj"
            :on-success="handleSuccess"
            :before-upload="beforeAvatarUpload"
            :show-file-list="moreUpload"
            :file-list="fileList"
            :on-preview="handlePictureCardPreview"
            :on-remove="handleRemove"
            :memory="memory"
            :fileType="fileType"
            :defaultImage="defaultImage"
        >
            <img v-if="imageUrl&&moreUpload==false" :src="imageUrl" class="avatar">
            <i v-if="!imageUrl&&moreUpload==false" class="el-icon-plus avatar-uploader-icon"></i>
            <i v-if="!imageUrl&&moreUpload" class="el-icon-plus"></i>
            <div slot="tip" class="el-upload__tip">{{tips}}</div>
        </el-upload>
        <el-dialog :visible.sync="dialogVisible" size="tiny">
            <img width="100%" :src="dialogImageUrl" alt>
        </el-dialog>
    </div>
</template>
<script>
import config from "../../../config/imageConfig.js";
export default {
    name: "images-upload",
    data() {
        return {
            config: config,
            imageUrl: null,
            dialogImageUrl: "",
            dialogVisible: false,
            typeStr: ""
        };
    },
    props: {
        moreUpload: {
            type: Boolean,
            default: function() {
                return true;
            }
        }, //多张上传 false只能一张
        url: {
            type: String,
            default: function() {
                return "http://10.54.26.117:8080//uploadPublish";
            }
        },
        index: null,
        name: null,
        fileType: {
            type: Array,
            default: function() {
                return ["image/png", "image/jpeg", "image/jpg"];
            }
        }, //支持文件格式
        tips: {
            type: String,
            default: function() {
                return "只支持png jpeg jpg 格式文件，内存不超过1MB，图片建议尺寸200*200";
            }
        },
        memory: {
            type: Number,
            default: function() {
                return 1;
            }
        }, //最大内存 1 = 1024*1024
        onSuccess: Function, //返回成功上传服务器图片地址，组件index
        onRemove: Function, //返回成功上传服务器图片地址，组件index
        uploadObj: Object,
        fileList: Array,
        defaultImage:null,//单张图片 显示
        // fileType:['image/png','image/jpeg','image/jpg'],//支持文件格式
        // beforeAvatarUpload: Function,
        // handlePictureCardPreview: Function,
    },
    created() {
        let typeStr = this.fileType.join("");
        this.typeStr = typeStr.replace(/image\//g, " ");
        this.imageUrl=this.defaultImage
    },
    methods: {
        //上传校验
        beforeAvatarUpload(file) {
            console.log(file)
            let isImagesType = this.fileType.includes(file.type);
            const maxMemory = file.size / 1024 / 1024 < this.memory;
            if (!isImagesType) {
                this.$message.error(`上传图片只能是 ${this.typeStr} 格式!`);
            }
            if (!maxMemory) {
                this.$message.error(`上传图片大小不能超过 ${this.memory}MB!`);
            }
            return isImagesType && maxMemory;
        },
        //上传成功
        handleSuccess(res) {
            console.log(res)
            if (res.result !== 'success') {
                this.$message.error(res.errMsg);
                return;
            }
            if (!this.moreUpload) {
                this.imageUrl = res.data;
            }

            this.onSuccess.call(this, res.data, this.index);
        },
        //预览
        handlePictureCardPreview(file) {
            this.dialogImageUrl = file.url;
            this.dialogVisible = true;
        },
        //删除
        handleRemove(file) {
            if (file.status === "success") {
                let imageUrl = file.response ? file.response.data : file.url;
                this.onRemove.call(this, imageUrl, this.index);
            }
        }
    },
    watch:{
        defaultImage(n){
            this.defaultImage=n
            this.imageUrl=n
        }
    }
};
</script>

<style>
.avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
}
.avatar-uploader .el-upload:hover {
    border-color: #409eff;
}
.avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 148px;
    height: 148px;
    line-height: 148px;
    text-align: center;
}
.avatar {
    width: 148px;
    height: 148px;
    display: block;
}
</style>