import md5 from 'js-md5'
import * as cookie from '../assets/js/cookie'

export default {
    headers: function createHeader(defaultAccept) {
        let token = '';
        let userId = '';
        let loginUser = cookie.getCookie('userInfo') ? cookie.getCookie('userInfo') : '';
        let nowtime = Date.now();
        let sign = '';
        userId = cookie.getCookie('userId')
        if (loginUser != '' && loginUser != null) {
            token = loginUser.token;
            userId = loginUser.adminId;
            let sign_str = userId + '' + nowtime.toString() + '63Isa23ahr2fghf23JOU3ad';
            sign = md5.hex(sign_str);
        } else {
            let sign_str = nowtime.toString() + '63Isa23ahr2fghf23JOU3ad';
            sign = md5.hex(sign_str);
        }
        return {
            'Accept': 'application/json',
            'Content-Type': defaultAccept ? null : 'application/json;charset=utf-8',
            'user': userId

        }
    }
}


