import Vue from 'vue';
import Router from 'vue-router';

Vue.use(Router);

export default new Router({
    base: '/gohome',
    mode: 'history',
    routes: [
        {
            path: '/',
            redirect: '/missingperson'
        },
        {
            path: '/',
            component: resolve => require(['../components/common/Home.vue'], resolve),
            meta: { title: 'GO HOME' },
            children: [

                 
                {
                    name: 'missingperson',
                    path: 'missingperson',
                    component: resolve => require(['../pages/missingperson/missingperson.vue'],resolve),

                },
                {
                    name: 'mypublish',
                    path: 'mypublish',
                    component: resolve => require(['../pages/missingperson/mypublish.vue'],resolve),

                },
                {
                    name: 'myconcern',
                    path: 'myconcern',
                    component: resolve => require(['../pages/missingperson/myconcern.vue'],resolve),

                },
                {
                    name: 'mythread',
                    path: 'mythread',
                    component: resolve => require(['../pages/missingperson/mythread.vue'],resolve),

                },

                {
                    name: 'goodsList',
                    path: '/goodsList',
                    component: resolve => require(['../pages/missingperson/mypublish.vue'],resolve),
                    //component: resolve => require(['../pages/goods/list.vue'], resolve),
                    meta: { title: '商品列表' }
                },
                {
                    name: 'goodsAdd',
                    path: '/goodsAdd',
                    component: resolve => require(['../pages/goods/add.vue'], resolve),
                    meta: { title: '添加商品' ,noTag: true}
                },
                {
                    name: 'goodsEdit',
                    path: '/goodsEdit',
                    component: resolve => require(['../pages/goods/edit.vue'], resolve),
                    meta: { title: '编辑商品',noTag: true }
                },



                {
                    name: 'orderList',
                    path: '/orderList',
                    component: resolve => require(['../pages/order/list.vue'], resolve),
                    meta: { title: '订单列表' }
                },

                {
                    name: 'bannerList',
                    path: '/bannerList',
                    component: resolve => require(['../pages/banner/list.vue'], resolve),
                    meta: { title: '轮播图列表' }
                },
                {
                    name: 'bannerAdd',
                    path: '/bannerAdd',
                    component: resolve => require(['../pages/banner/add.vue'], resolve),
                    meta: { title: '添加轮播图' ,noTag: true}
                },
                {
                    name: 'bannerEdit',
                    path: '/bannerEdit',
                    component: resolve => require(['../pages/banner/edit.vue'], resolve),
                    meta: { title: '编辑轮播图',noTag: true }
                },

                {
                    name: 'categoryList',
                    path: '/categoryList',
                    component: resolve => require(['../pages/category/list.vue'], resolve),
                    meta: { title: '分类列表'}
                },

                {
                    name: 'userList',
                    path: '/userList',
                    component: resolve => require(['../pages/user/list.vue'], resolve),
                    meta: { title: '用户'}
                },


                {
                    path: '/dashboard',
                    component: resolve => require(['../components/page/Dashboard.vue'], resolve),
                    meta: { title: '系统首页' }
                },
                {
                    path: '/icon',
                    component: resolve => require(['../components/page/Icon.vue'], resolve),
                    meta: { title: '自定义图标' }
                },
                {
                    path: '/table',
                    component: resolve => require(['../components/page/BaseTable.vue'], resolve),
                    meta: { title: '基础表格' }
                },
                {
                    path: '/tabs',
                    component: resolve => require(['../components/page/Tabs.vue'], resolve),
                    meta: { title: 'tab选项卡' }
                },
                {
                    path: '/form',
                    component: resolve => require(['../components/page/BaseForm.vue'], resolve),
                    meta: { title: '基本表单' }
                },
                {
                    // 富文本编辑器组件
                    path: '/editor',
                    component: resolve => require(['../components/page/VueEditor.vue'], resolve),
                    meta: { title: '富文本编辑器' }
                },
                {
                    // markdown组件
                    path: '/markdown',
                    component: resolve => require(['../components/page/Markdown.vue'], resolve),
                    meta: { title: 'markdown编辑器' }
                },
                {
                    // 图片上传组件
                    path: '/upload',
                    component: resolve => require(['../components/page/Upload.vue'], resolve),
                    meta: { title: '文件上传' }
                },
                {
                    // vue-schart组件
                    path: '/charts',
                    component: resolve => require(['../components/page/BaseCharts.vue'], resolve),
                    meta: { title: 'schart图表' }
                },
                {
                    // 拖拽列表组件
                    path: '/drag',
                    component: resolve => require(['../components/page/DragList.vue'], resolve),
                    meta: { title: '拖拽列表' }
                },
                {
                    // 拖拽Dialog组件
                    path: '/dialog',
                    component: resolve => require(['../components/page/DragDialog.vue'], resolve),
                    meta: { title: '拖拽弹框' }
                },
                {
                    // 国际化组件
                    path: '/i18n',
                    component: resolve => require(['../components/page/I18n.vue'], resolve),
                    meta: { title: '国际化' }
                },
                {
                    // 权限页面
                    path: '/permission',
                    component: resolve => require(['../components/page/Permission.vue'], resolve),
                    meta: { title: '权限测试', permission: true }
                },
                {
                    path: '/404',
                    component: resolve => require(['../components/page/404.vue'], resolve),
                    meta: { title: '404' }
                },
                {
                    path: '/403',
                    component: resolve => require(['../components/page/403.vue'], resolve),
                    meta: { title: '403' }
                }
            ]
        },
        {
            path: '/login',
            component: resolve => require(['../pages/login.vue'], resolve)
        },
        {
            path: '*',
            redirect: '/404'
        }
    ]
})
