<template>
    <!-- 分页 -->
    <div class="pager">
        <el-pagination class="float-right" layout="total, sizes, prev, pager, next, jumper" :total="pager.total"
            :current-page.sync="pager.page" :page-size="pager.rows" :page-sizes="[20, 30, 50, 100]" @size-change="onChangeSize"
            @current-change="onChangePage">
        </el-pagination>
    </div>
</template>

<script>
    export default {
        name: "my-page",
        props: ['pager'],
        computed: {
            total() {
                return this.pager.total;
            },
            // 检测是否获取到无数据
            initBack() {
                return this.pager.total / this.pager.rows < this.pager.page;
            },
        },
        watch: {
            total() {
                // 存在记录但未获取到数据时, 重新请求
                if (this.initBack) {
                    this.pager.page -= 1;
                    this.$emit('change');
                }
            },
        },
        methods: {
            // 每页条数
            onChangeSize(rows) {
                this.pager.rows = rows;
                this.$emit('change');
            },
            // 翻页
            onChangePage(page) {
                this.pager.page = page;
                this.$emit('change');
            },
        },
    };

</script>

<style scoped>
    .pager {
        padding: 8px 0 0px;
    }

    .el-pagination {
        padding: 0;
    }

</style>
